          <slot />
          <PieDePagina />
          <AvisoPrivacidadMovilModal />
          <ModalBaseMovil id="modalBD"/>          
          <Compartir />
      </div>
      <div class=" mr-2 contenido2" v-if="esBaseDatos ">
