          <slot />
          <PieDePagina />
          <AvisoPrivacidadMovilModal />
          <ModalBaseMovil id="modalBD"/>          
          <Compartir />
      </div>

      
    </div>
    <div class="container-fluid" v-else>
      <div class="row">
        <div class="col-12">
          <div class="cargando">
            <div>
              <Spinner />
              <p class="text-center my-5">
                Cargando contenido...
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
